<template>
    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
        <div>
            <b-progress height="1.5rem" :value="type=='All'?consumed:AllowedStorage" :max="type=='All'?total:allocated" class=""></b-progress>
        </div>
        <div class="level_info my-2 d-block float-left w-100" style="height:auto;">
            <div class="d-flex bd-highlight">
                <div class="p-2 bd-highlight font-12" v-if="type=='Doc'">
                    <b id="divClientStorage pr-1"><span></span>  <span class="clr-1">
                        {{AllowedClientStoragePercentage}}% ({{formatBytes(AllowedClientStorage)}})</span> 
                    </b> <span>Allocated to Client</span>
                </div> 
                <div class="p-2 bd-highlight font-12" v-if="type=='Doc'">
                    <b id="divConsumendStorage pr-1"><span></span>   <span class="clr-2">
                        {{AllowedUserStoragePercentage}}% ({{formatBytes(AllowedUserStorage)}})</span> 
                    </b> <span>Allocated to User</span>
                </div>
                <div class="p-2 bd-highlight font-12"  v-if="type=='All'">
                    <b id="divFreeStorage"><span></span></b>
                    <span class="clr-1 pr-1">{{ConsumedPercentage}}% ({{formatBytes(consumed)}}) </span>
                    <span>Storage Consumed</span>
                </div>
                <div class="p-2 bd-highlight font-12">
                    <b id="divFreeStorage"><span></span></b>
                    <span class="clr-3 pr-1">{{AvailablePercentage}}% ({{formatBytes(AvailableSpace)}}) </span>
                    <span>Available Storage</span>
                </div>
                <div class="ml-auto p-2 bd-highlight font-12">
                    <b id="divFreeStorage"><span></span></b><span class="font-weight-bold pr-1">{{formatBytes(type=='All'?total:allocated)}}
                        </span> <span>Total Storage</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../services/DataService';
export default {
        props:{
            type:{
                type:String,
                required:true    
            }
        },
        data() {
            return {
                total:null,
                consumed:null,
                allocated:null,
                AllowedClientStorage:null,
                AllowedUserStorage:null
            }
        },
        created: function(){
            var vm=this;
            vm.GetStoragedata();
        },
        computed: {
            // ConsumedPercentage:function(){
            // return (parseFloat((this.consumed*100)/this.total).toFixed(0));
            // },
            ConsumedPercentage: {
            // getter
            get: function () {
                return (parseFloat((this.consumed*100)/this.total).toFixed(0))
            },
            // setter
            set: function (newValue) {
                
            }
           },
            AllocatedPercentage(){
                return (parseFloat((this.allocated*100)/this.total).toFixed(0));
            },
            AvailableSpace(){
                return this.type=='All'?(this.total>=this.consumed?this.total-this.consumed:0):(this.AllowedClientStorage + this.AllowedUserStorage < this.allocated? this.allocated - this.AllowedClientStorage - this.AllowedUserStorage:0);
            },
            AvailablePercentage(){
                if(this.type=='All'){
                    var val=this.total>=this.consumed?this.total-this.consumed:0;
                    return (parseFloat((val*100)/this.total).toFixed(0));
                }
                else{
                    var val=this.AllowedClientStorage + this.AllowedUserStorage < this.allocated ? this.allocated - this.AllowedClientStorage - this.AllowedUserStorage:0;
                    return (parseFloat((val*100)/this.allocated).toFixed(0));
                }
            },
            AllowedStorage(){
                var val=this.AllowedClientStorage + this.AllowedUserStorage;
                return val;
            },
            AllowedStoragePercentage(){
                var val=(parseFloat(((this.AllowedClientStorage + this.AllowedUserStorage)*100)/this.allocated).toFixed(0));
                return val;
            },
            AllowedClientStoragePercentage(){
                var val=(parseFloat((this.AllowedClientStorage*100)/(this.allocated)).toFixed(0));
                return val;
            },
            AllowedUserStoragePercentage(){
                var val=(parseFloat((this.AllowedUserStorage*100)/(this.allocated)).toFixed(0));
                return val;
            }
        },
        watch: {
            consumed: function (value) {
                this.ConsumedPercentage =  (parseFloat((this.consumed*100)/this.total).toFixed(0));
            }
        },
        methods: {
            GetStoragedata: function () {
                
                var vm = this;
                 DataService.GetStorageReport().then(function (response) {
                    vm.total = response.data[0].CONTAINER_LIMIT==null||response.data[0].CONTAINER_LIMIT==null?0:response.data[0].CONTAINER_LIMIT;
                    vm.consumed = response.data[0].CONTAINER_SIZE==null||response.data[0].CONTAINER_SIZE==undefined?0:response.data[0].CONTAINER_SIZE;
                    vm.allocated =  response.data[0].DOC_STORAGE_MAXLIMIT==null||response.data[0].DOC_STORAGE_MAXLIMIT==undefined?o:response.data[0].DOC_STORAGE_MAXLIMIT;
                });
                if(vm.type=='Doc'){
                    var params = `pageNo=${1}&pageSize=${10}&sortBy=&sortExp=&userTypeIds=4`;
                        DataService.GetUserStorageDetail(params).then(function (response) {

                        var ClientData = JSON.parse(response.data).USERSTORAGEDETAILLIST;

                        vm.AllowedClientStorage = 0;
                        ClientData.forEach(x => {
                            if(x.ALLOWED_STORAGE==null||x.ALLOWED_STORAGE==undefined){
                                x.ALLOWED_STORAGE=0;
                            vm.AllowedClientStorage += x.ALLOWED_STORAGE;
                            }
                            else{
                                vm.AllowedClientStorage += x.ALLOWED_STORAGE;
                            }
                        })
                    });

                    var userParams = `pageNo=${1}&pageSize=${10}&sortBy=&sortExp=&userTypeIds=1,2,3`;

                    DataService.GetUserStorageDetail(userParams).then(function (response) {
                        
                        var UserData = JSON.parse(response.data).USERSTORAGEDETAILLIST;
                       
                        vm.AllowedUserStorage = 0;
                        UserData.forEach(x => {
                            if(x.ALLOWED_STORAGE==null||x.ALLOWED_STORAGE==undefined){
                                x.ALLOWED_STORAGE=0;
                                vm.AllowedUserStorage += x.ALLOWED_STORAGE;
                            }
                            else{
                                vm.AllowedUserStorage += x.ALLOWED_STORAGE;
                            }
                           
                        })
                    });
                }
                vm.total = isNaN(vm.total)?0:vm.total;
                vm.consumed = isNaN(vm.consumed)?0:vm.consumed;
                vm.allocated = isNaN(vm.allocated)?0:vm.allocated;
                vm.AllowedClientStorage = isNaN(vm.AllowedClientStorage)?0:vm.AllowedClientStorage;
                vm.AllowedUserStorage = isNaN(vm.AllowedUserStorage)?0:vm.AllowedUserStorage;
            },
            formatBytes: function(bytes, isNumeric=false , decimals = 2) {
                if (!+bytes) return '0 Bytes'

                const k = 1024
                const dm = decimals < 0 ? 0 : decimals
                // const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
                const sizes = ['MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
                const i = Math.floor(Math.log(bytes) / Math.log(k));
                if(isNumeric){
                    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm));
                }
                else{
                    return `${parseFloat((bytes / Math.pow(k, i)).toFixed(dm))} ${sizes[i]}`;
                }
            }
        }
    }
</script>
